import React, { useState } from 'react';

const CourseTop = () => {
    const [isExpanded, setIsExpanded] = useState(false);

    const toggleExpansion = () => {
        setIsExpanded(!isExpanded);
    };

    return (
        <div className="flex flex-col text-grey justify-center">
            <h2 className="text-2xl font-bold mb-4 leading-tight">
                What is Java Developer Training?
            </h2>
            <div
                className="text-lg overflow-hidden transition-all duration-700"
                style={{ maxHeight: isExpanded ? 'none' : '80px' }}
            >
                <p className="text-justify">
                    Java Developer training is designed to equip learners with comprehensive skills for both front-end and back-end web development using Java technologies. Java is a versatile programming language widely used in enterprise applications, web development, mobile applications, and more.
                </p>
                <p className="text-justify mt-4">
                    This course covers essential topics such as Java programming fundamentals, object-oriented programming (OOP), relational database management systems (RDBMS) with SQL, web application development with Spring Boot, RESTful APIs, front-end development with frameworks like React or Angular, and deployment strategies.
                </p>
                <p className="text-justify mt-4">
                    Students will learn to build dynamic and scalable web applications, integrate databases, handle user authentication, and deploy applications to cloud platforms. Java Developer training also emphasizes software engineering principles, agile methodologies, and collaborative project work.
                </p>
                <p className="text-justify mt-4">
                    By the end of the course, learners will have a solid foundation in full stack Java development, ready to pursue careers as Java developers, software engineers, or web developers in various industries.
                </p>
                <p />
            </div>
            <button
                className="underline text-left mt-3 focus:outline-none hover:text-orange self-center"
                onClick={toggleExpansion}
            >
                {isExpanded ? 'Show less' : 'Show more'}
            </button>
        </div>
    );
};

export default CourseTop;
