import React, { useState } from 'react';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa'; // Ensure you have react-icons installed

const FAQ = () => {
  const [openFAQ, setOpenFAQ] = useState(null);

  const toggleFAQ = (index) => {
    setOpenFAQ(openFAQ === index ? null : index);
  };

  const faqs = [
    {
      question: "What is Java Developer Training?",
      answer: "Java Developer training focuses on equipping learners with comprehensive skills for Java programming. It covers essential topics such as Java fundamentals, object-oriented programming (OOP), data structures, algorithms, and application development. Java is widely used for backend development, desktop applications, and Android app development.",
    },
    {
      question: "What are the key skills taught in Java Developer Training?",
      answer: "Key skills taught in Java Developer training include proficiency in Java programming, understanding of object-oriented principles, knowledge of frameworks like Spring and Hibernate, database management with SQL, web development, and application deployment.",
    },
    {
      question: "Why choose Java for software development?",
      answer: "Java is chosen for software development due to its platform independence, strong community support, scalability, and security features. It is widely used in enterprise applications, financial services, Android app development, and large-scale web applications.",
    },
    {
      question: "What career opportunities are available after Java Developer Training?",
      answer: "After completing Java Developer training, individuals can pursue careers such as Java Developer, Software Engineer, Application Developer, Full Stack Developer (with additional frontend skills), Android Developer, and more. Java skills are highly valued in the software industry.",
    },
    {
      question: "How can I prepare for Java Developer Training?",
      answer: "To prepare for Java Developer training, it is recommended to have a basic understanding of programming concepts and logic. Familiarize yourself with Java syntax, data types, and control structures. Practicing coding exercises and building small projects can also be beneficial.",
    },
    {
      question: "What are the advantages of learning Java?",
      answer: "Learning Java offers advantages such as cross-platform compatibility, object-oriented approach, robustness, scalability, community support, and a wide range of libraries and frameworks. It is suitable for developing diverse applications from web applications to mobile apps and enterprise systems.",
    },
    {
      question: "Is Java suitable for beginners?",
      answer: "Yes, Java is considered suitable for beginners due to its straightforward syntax, extensive documentation, and vast community support. It provides a structured approach to programming and helps beginners understand fundamental concepts like variables, data types, and control flow.",
    },
    {
      question: "What are some popular frameworks and tools used in Java development?",
      answer: "Popular frameworks and tools in Java development include Spring Framework (including Spring Boot), Hibernate for ORM, Maven and Gradle for build automation, Apache Kafka for messaging, JUnit for testing, and Jenkins for continuous integration and deployment.",
    },
    {
      question: "How does Java support multi-threading?",
      answer: "Java supports multi-threading through its built-in features like threads and synchronization. Developers can create and manage multiple threads within a Java application to achieve concurrent execution and improve performance in tasks such as handling multiple client requests or processing data in parallel.",
    },
    {
      question: "What is the role of Java in modern web development?",
      answer: "In modern web development, Java is used primarily on the server-side to build scalable and robust backend systems. It integrates with front-end frameworks like React or Angular through RESTful APIs. Java frameworks such as Spring Boot facilitate rapid development and deployment of web applications.",
    },
    {
      question: "How can Java be used for Android app development?",
      answer: "Java is the official language for Android app development. Android Studio, the official IDE for Android development, supports Java as the primary programming language. Developers use Java to build Android applications, leveraging Android SDK libraries and APIs for creating interactive and feature-rich mobile apps.",
    },
  ];

  return (
    <div className="container mx-auto mt-16">
      <h2 className="text-center text-xl md:text-3xl font-bold mb-10 leading-tight">
        Frequently Asked Questions about Java Developer Training
      </h2>
      <div className="flex flex-col gap-4">
        {faqs.map((faq, index) => (
          <section key={index} className="border-b pb-2">
            <div
              className="flex justify-between items-center cursor-pointer"
              onClick={() => toggleFAQ(index)}
            >
              <h3 className="font-semibold text-lg">{faq.question}</h3>
              {openFAQ === index ? <FaChevronUp /> : <FaChevronDown />}
            </div>
            {openFAQ === index && <p className="mt-2">{faq.answer}</p>}
          </section>
        ))}
      </div>
    </div>
  );
};

export default FAQ;
